import { Component,OnInit,HostBinding } from '@angular/core';
import { Categoria } from 'src/app/models/Categorias';
import {  CategoriasService } from '../../services/categorias.service';
import { ActivatedRoute,Router } from '@angular/router';


@Component({
  selector: 'app-categorias-form',
  templateUrl: './categorias-form.component.html',
  styleUrls: ['./categorias-form.component.css']
})
export class CategoriasFormComponent implements OnInit {

  @HostBinding('class') classes ='row';

  categoria: Categoria = {
    idCategoria: 0,
    nombreCategoria:'',
    cantidadLibros:0,
    editorial:'',
    fechaIngreso: new Date()
    

    };
    edit: boolean = false;

    constructor(private categoriasService: CategoriasService, private router:Router,private activedRoute:ActivatedRoute ) { }

    ngOnInit() {
      const params = this.activedRoute.snapshot.params;
      //console.log(params);
      if (params.idCategorias){
        console.log(params);
        console.log('funciona');
        this.categoriasService.getCategoria(params.idCategorias)
        .subscribe(
          res =>{
            console.log(res);
            this.categoria = res;
            this.edit = true;
          },
          
          err =>  console.error(err)
          
          
        )
      }
    }
    saveNewCategoria(){
      //delete this.categoria.fechaIngreso;
      delete this.categoria.idCategoria;
      this.categoriasService.saveCategoria(this.categoria)
      .subscribe(
        res =>{
          console.log(res);
          this.router.navigate(['/categoria']); 
        },
        err =>console.error(err)
        
      )
    }
    updateCategorias(){
      //delete this.categoria.fechaIngreso;
      this.categoriasService.updateCategoria(String(this.categoria.idCategoria) ,this.categoria )
      .subscribe(
        res =>{
          console.log(res);
          this.router.navigate(['/categoria']);
        },
        err =>console.log(err)
      )
    }
  
  }
  