import { Component, OnInit, HostBinding } from '@angular/core';
import { Libro } from 'src/app/models/Libros';
import { LibrosService } from '../../services/libros.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Categoria } from 'src/app/models/Categorias';
import { CategoriasService } from '../../services/categorias.service';


@Component({
  selector: 'app-libros-form',
  templateUrl: './libros-form.component.html',
  styleUrls: ['./libros-form.component.css']
})
export class LibrosFormComponent implements OnInit {

  @HostBinding('class') classes = 'row';

  libro: Libro = {
    clave: 0,
    categoria_idcategorias: 0,
    nombre: '',
    image:'',
    edicion: 0,
    cantidadTomos: 0,
    precio: 0,
    autor: '',
    horaEntrega: new Date()


  };

  categoria:any=[];
  traerCategorias(){
    this.categoriasService.getCategorias().subscribe(
      res => {
        this.categoria=res;
      },
      err => console.error(err)

    );

  }

 

  edit: boolean = false;

  constructor(private librosService: LibrosService, private categoriasService: CategoriasService, private router: Router, private activedRoute: ActivatedRoute) { }

  ngOnInit() {
    const params = this.activedRoute.snapshot.params;
    //console.log(params);
    this.traerCategorias();
    if (params.clave) {
      console.log(params);
      console.log('funciona');
      this.librosService.getCategorias2();
      this.librosService.getLibro(params.clave)


        .subscribe(
          res => {
            console.log(res);
            this.libro = res;
            this.edit = true;
          },

          err => console.error(err)


        )
    }
  }
  saveNewLibro() {
    //delete this.categoria.fechaIngreso;
    delete this.libro.clave;
    console.log(this.libro);
    if (!(this.libro.nombre == '' || this.libro.precio == 0 || this.libro.edicion == 0 || this.libro.categoria_idcategorias == 0 || this.libro.autor == '' || this.libro.cantidadTomos == 0 )) {
      this.librosService.saveLibro(this.libro)
        .subscribe(
          res => {
            console.log(res);
            this.router.navigate(['/libro']);
          },
          err => console.error(err)

        )
    }
    else {
      alert('Lena todos los campos para continuar');
      console.log(this.libro);
    }
    

  }
  updateLibro() {
    if (!(this.libro.nombre == '' || this.libro.precio == 0 || this.libro.edicion == 0 || this.libro.categoria_idcategorias == 0 || this.libro.autor == '' || this.libro.cantidadTomos == 0 )) {
      this.librosService.updateLibro(String(this.libro.clave), this.libro)
        .subscribe(
          res => {
            console.log(res);
            this.router.navigate(['/libro']);
          },
          err => console.log(err)
        )
    }
    else {
      alert('Lena todos los campos para continuar');
      console.log(this.libro);
    }
  }

}
