import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { CategoriasListComponent }  from './components/categorias-list/categorias-list.component';
import { CategoriasFormComponent } from './components/categorias-form/categorias-form.component';
import { LibrosListComponent } from './components/libros-list/libros-list.component';
import { LibrosFormComponent } from './components/libros-form/libros-form.component';



const routes: Routes = [

  {
    path:'',
    redirectTo:'/libro',
    pathMatch:'full'
  },
  {
    path: 'categoria',
    component:CategoriasListComponent
  },
  {
    path:'categoria/add',
    component:CategoriasFormComponent
  },
  {
    path: 'categoria/edit/:idCategorias',
    component: CategoriasFormComponent
  },

  {
    path:'libro',
    component:LibrosListComponent
  },
  
  {
    path:'libro/add',
    component:LibrosFormComponent
  },
  {
    path:'libro/edit/:clave',
    component:LibrosFormComponent
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
